<template>
  <div class="goods">
      <options :data="dataOption" v-on:change="optchange" ref="goodsOpt"/>
      <tables :data="dataTable" :list="tableList" v-on:change="tabchange"/>
      <div class="demonstration-box">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageParams.size"
          layout="total, prev, pager, next"
          :total="pageParams.totalCount"
        ></el-pagination>
      </div>
      <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <span>{{dialogContent}}</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="sure">确 定</el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
import Options from 'views/common/options'
import Tables from 'views/common/tables'
import queryString from "querystring"
import page from 'data/page'
import { DateDay, ScondDay } from "utils/formData";

const dataOption = {
  leftMenu: [
    { id:'1', name:'商品名称：', placeholder:'请输入商品名称', module:'name'},
    { id:'2', name:'商品数量：', placeholder:'请输入商品数量', module:'count'},
    // { id:'3', name:'商品价格：', placeholder:'请输入商品价格', module:'price'},
    // { id:'4', name:'开始日期：', placeholder:'请选择开始日期', module:'beginTime', type:'date'},
    // { id:'5', name:'结束日期：', placeholder:'请选择结束日期', module:'endTime', type:'date'},
  ],
  rightMenu: [
    { id:'r1', name:'查询'},
    { id:'r2', name:'新增'}
  ]
}

const dataTable = {
  table: [
    { label:'商品名称', prop:'name'},
    { label:'商品价格', prop:'count'},
    { label:'商品数量', prop:'price'},
    { label:'时间范围', prop:'time'},
  ],
  edit: [
    {name: '编辑', id: 't1'},
    {name: '删除', id: 't2'},
  ]
}

const tips = ['确认删除该条信息?'];

export default {
  name: 'goods',
  mixins: [page],
  data() {
    return {
      dataOption: dataOption,
      dataTable: dataTable,
      dialogVisible: false,
      dialogContent: tips[0],
      tableList: [],
      loading: false,
      currentPage: 1,
      id: ''
    }
  },
  components: {
    Options,
    Tables
  },
  mounted(){
    this.search();
  },
  methods:{
    optchange(params){
      console.log(params.id + '--id被单击了')
      if(params.id === 'r1') return this.search();
      if(params.id === 'r2') return this.$router.push(`goodsAdd?type=new`);
    },
    tabchange(params){
      console.log(params.id + '--id被单击了')

      this.id = params.data.id;
      // 编辑
      if(params.id === 't1') return this.$router.push(`goodsAdd?type=edit&id=${this.id}`);
      // 删除
      if(params.id === 't2') return this.dialogVisible = true;
    },
    sure(){
      this.api.delete(`/goods/${this.id}`).then((res)=>{
        this.loading = false;
        this.search();
        this.$message(res.data);     
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
      this.dialogVisible = false;
    },
    search(){  //搜索
      const optParams = this.$refs.goodsOpt.getData();
      const pageParams = { size: 20, page: this.currentPage -1 };
      const params = Object.assign({}, optParams, pageParams);
      const param = queryString.stringify(params || {});

      this.loading = true;
      this.api.get(`/goods?${param}`).then((res)=>{
        this.loading = false;
        this.dataInit(res.data);
        this.setPage(res.data);
        this.tableList = res.data.list;
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
    },
    dataInit(data){//数据初始化
        const tableList = data.list.map(item => {
            if(item.beginTime && item.endTime)
            item.time = ScondDay(item.beginTime) + ' 至 ' + ScondDay(item.endTime);
        })
        this.tableList = tableList;
    }
  }
}
</script>
 
<style lang="less" scoped>

</style>
 